import React, { useEffect, useState } from 'react'
import { FcDocument } from 'react-icons/fc'
import BackendUrl from '../../../Components/ApiList/BackendUrl'
import photo from '../../../Components/Media/photo.png'

function PilotWorkflowDocumentRow(props) {
    const base_url = BackendUrl
    const [errorImage, seterrorImage] = useState(false)

    useEffect(() => {
        seterrorImage(false)
    },[])

    console.log('document base url at doc show....', props?.api?.documentBaseUrl)
    return (
        <>
            <tr>

                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <p className="text-gray-900 whitespace-no-wrap">
                        {props?.index + 1}
                    </p>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    <p className="text-gray-900 whitespace-no-wrap">
                        {props?.docList?.doc_code}
                    </p>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm cursor-pointer" onClick={() => props.openModal(`${base_url}/${props?.docList?.doc_path}`, props?.docList?.doc_path?.split('.')[1])}>
                <div className="flex items-center">
                        {(props?.docList?.doc_path?.split('.')[1] == 'pdf' || props?.docList?.doc_path?.split('.')[1] == 'pdf/') &&
                            <div className="flex-shrink-0 text-[28px]">
                                <FcDocument />
                            </div>
                        }
                        {(props?.docList?.doc_path?.split('.')[1] == 'jpg' || props?.docList?.doc_path?.split('.')[1] == 'jpg/') &&
                            <div className="flex-shrink-0 px-1 py-2">
                                <img src={errorImage ? photo : `${base_url}/${props?.docList?.doc_path}`} onError={() => seterrorImage(true)} className="md:w-[2vw] w-[5vw]" alt="" srcset="" />
                            </div>
                        }
                        {(props?.docList?.doc_path?.split('.')[1] == 'jpeg' || props?.docList?.doc_path?.split('.')[1] == 'jpeg/') &&
                            <div className="flex-shrink-0 px-1 py-2">
                                <img src={errorImage ? photo : `${base_url}/${props?.docList?.doc_path}`} onError={() => seterrorImage(true)} className="md:w-[2vw] w-[5vw]" alt="" srcset="" />
                            </div>
                        }
                        {(props?.docList?.doc_path?.split('.')[1] == 'png' || props?.docList?.doc_path?.split('.')[1] == 'png/') &&
                            <div className="flex-shrink-0 px-1 py-2">
                                <img src={errorImage ? photo : `${base_url}/${props?.docList?.doc_path}`} onError={() => seterrorImage(true)} className="md:w-[2vw] w-[5vw]" alt="" srcset="" />
                            </div>
                        }

                    </div>
                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    {
                        props?.docList?.doc_code === 'PHOTOGRAPH' && <p className="whitespace-no-wrap">
                        </p>
                    }
                    {
                        props?.docList?.doc_code !== 'PHOTOGRAPH' && <>
                            <p className="whitespace-no-wrap">
                                {props?.docList?.verify_status == 0 && <>Pending</>}</p>
                            <p className="text-green-500 whitespace-no-wrap">
                                {props?.docList?.verify_status == 1 && <>Verified</>}</p>
                            <p className="text-red-500 whitespace-no-wrap">
                                {props?.docList?.verify_status == 2 && <>Rejected</>}
                            </p>
                        </>
                    }

                </td>
                <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                    {props?.docList?.remarks == '' ? <i>N/A</i> : props?.docList?.remarks}
                </td>
            </tr>
        </>
    )
}

export default PilotWorkflowDocumentRow
